$main-color: #a41638;
$second-color:gray;
$third-color:rgb(48,48,48);
$white-color:#fff;
$orange-color:rgb(248,145,37);
$full-width:100%;

body{
  //background-color: $third-color;
  background-image: url('../images/login-bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  color: $white-color;
  padding-right: 30px;
  padding-left: 30px;
  padding-top: 30px;
  height: 100%;
}

.logo{
  padding: 10px;
  margin-bottom: 15px;
}

.user-login {
  font-family:Arial, FontAwesome;
  color: $second-color;
  label{
    margin-right: 5px;
  }
  input{
    width: 85%;
    height: 42px;
    outline: none;
    color: #505050;
    border: none;
    border-bottom: 1px solid #a9a9a9;
    background-color: transparent;
  }
  input[type=Submit]{
    background-color: $main-color;
    color: $white-color;
    width: 100%;
    border-radius: 5px;
    font-size: 20px;
    border:none;
  }
  input[type=Submit]:visited{
    background-color: $main-color;
    color: $white-color;
    width: 50%;
    border:none;
  }
  input[type=Submit]:hover{
    color: $white-color;
    transition: .7s;
    border:none;
  }
}

.hr-block{
  hr{
    width: 100%;
  }
  .or-block{
    background-color: #20374a;
    color: $white-color;
    border-radius: 20px;
    position: relative;
    top: 29px;
    padding: 10px;
  }
}

.services{
  padding: 20px 0px;
  .fa{
    font-size: 28px;
    padding: 10px 0px;
  }
}

.create-account{
  position: fixed;
  bottom: 0px;
  right: 0px;
  left: 0px;
  //margin-top: 80px;
}
